<template>
  <div class="cjwt_wrap">
    <headers :title="title"></headers>
    <p :class="style" @click="changeS"><span>商品购买支付方式有哪些?</span></p>
    <div v-show="style === 'down'">支持微信、支付宝、银联、黑卡签单或黑卡钱包支付。若一次性消费满100元以上，使用黑卡签单支付，还可以分期付款</div>
  </div>
</template>
<script>
  import "lib-flexible"
  import headers from '../components/editHeader.vue'
  export default {
    data() {
      return {
        title: "支付问题",
        style: 'down'
      };
    },
    components: {
      headers
    },
    methods: {
      changeS: function () {
        if(this.style === 'down'){
          this.style = 'up'
        }else {
          this.style = 'down'
        }
      }
    }
  };
</script>
<style lang="less" scoped>
  .cjwt_wrap {
    flex: 1;
    background: #F8F8FF;
  }

  .down{
    background: #fff url('../assets/cjwt/hotel_down.png') no-repeat center right 20px;
    background-size: 27px 15px;
    border-bottom: 1px solid #cacaca;
  }

  .up{
    background: #fff url('../assets/cjwt/up.png') no-repeat center right 20px;
    background-size: 27px 15px;
  }

  p {
    height: 106px;
    line-height: 106px;
    width: 670px;
    margin: 0 auto;
    margin-top: 110px;
    padding: 0 20px;
    display: flex;
    span {
      width: 100%;
      font-size: 32px;
      color: #000;
    }
  }

  div {
    font-size: 32px;
    color: #000;
    width: 670px;
    margin: 0 auto;
    padding: 0 20px;
    padding-top: 36px;
    padding-bottom: 38px;
    background: #fff;
    line-height: 52px;
  }
</style>



